import { BrowserModule } from '@angular/platform-browser';
import { ErrorHandler, NgModule } from '@angular/core';
import { IonicApp, IonicErrorHandler, IonicModule } from 'ionic-angular';
import { HttpModule } from "@angular/http";
// Native
import { SplashScreen } from '@ionic-native/splash-screen';
import { StatusBar } from '@ionic-native/status-bar';
import { InAppBrowser } from '@ionic-native/in-app-browser';
// Providers
import { AgoraMobile } from './app.component';
import { GemProvider } from '../providers/gem/gem.provider';
import { CardProvider } from '../providers/card/card.provider';
import { HeroProvider } from '../providers/hero/hero.provider';
import { AgoraCacheProvider } from '../providers/agora-cache/agora-cache.provider';
import { EpicAccountProvider } from "../providers/epic/epic-account.provider";
import { SideMenuProvider } from "../providers/side-menu/side-menu.provider";
import { ProfileProvider } from "../providers/profile/profile.provider";
import { AppProvider } from "../providers/app/app.provider";
import { StatsProvider } from "../providers/stats/stats.provider";
import { MatchProvider } from "../providers/match/match.provider";
import { HttpClientModule } from "@angular/common/http";
import { LeaderboardProvider } from "../providers/leaderboard/leaderboard.provider";

@NgModule({
    declarations: [
        AgoraMobile
    ],
    imports: [
        BrowserModule,
        IonicModule.forRoot(AgoraMobile, {}, 
{
  links: [
    { loadChildren: '../pages/card-summary/card-summary.module.ngfactory#CardSummaryPageModuleNgFactory', name: 'CardSummaryPage', segment: 'card-summary', priority: 'low', defaultHistory: [] },
    { loadChildren: '../pages/cards/cards.module.ngfactory#CardsPageModuleNgFactory', name: 'CardsPage', segment: 'cards', priority: 'low', defaultHistory: [] },
    { loadChildren: '../pages/decks/decks.module.ngfactory#DecksPageModuleNgFactory', name: 'DecksPage', segment: 'decks', priority: 'low', defaultHistory: [] },
    { loadChildren: '../pages/elo-detail/elo-detail.module.ngfactory#EloDetailPageModuleNgFactory', name: 'EloDetailPage', segment: 'elo-detail', priority: 'low', defaultHistory: [] },
    { loadChildren: '../pages/faq/faq.module.ngfactory#FaqPageModuleNgFactory', name: 'FaqPage', segment: 'faq', priority: 'low', defaultHistory: [] },
    { loadChildren: '../pages/gems/gems.module.ngfactory#GemsPageModuleNgFactory', name: 'GemsPage', segment: 'gems', priority: 'low', defaultHistory: [] },
    { loadChildren: '../pages/guides/guides.module.ngfactory#GuidesPageModuleNgFactory', name: 'GuidesPage', segment: 'guides', priority: 'low', defaultHistory: [] },
    { loadChildren: '../pages/hero-stats/hero-stats.module.ngfactory#HeroStatsPageModuleNgFactory', name: 'HeroStatsPage', segment: 'hero-stats', priority: 'low', defaultHistory: [] },
    { loadChildren: '../pages/hero-summary/hero-summary.module.ngfactory#HeroSummaryPageModuleNgFactory', name: 'HeroSummaryPage', segment: 'hero-summary', priority: 'low', defaultHistory: [] },
    { loadChildren: '../pages/leaderboards/leaderboards.module.ngfactory#LeaderboardsPageModuleNgFactory', name: 'LeaderboardsPage', segment: 'leaderboards', priority: 'low', defaultHistory: [] },
    { loadChildren: '../pages/match-summary/match-summary.module.ngfactory#MatchSummaryPageModuleNgFactory', name: 'MatchSummaryPage', segment: 'match-summary', priority: 'low', defaultHistory: [] },
    { loadChildren: '../pages/matches/matches.module.ngfactory#MatchesPageModuleNgFactory', name: 'MatchesPage', segment: 'matches', priority: 'low', defaultHistory: [] },
    { loadChildren: '../pages/privacy-policy/privacy-policy.module.ngfactory#PrivacyPolicyPageModuleNgFactory', name: 'PrivacyPolicyPage', segment: 'privacy-policy', priority: 'low', defaultHistory: [] },
    { loadChildren: '../pages/profile-menu-popover/profile-menu-popover.module.ngfactory#ProfileMenuPopoverPageModuleNgFactory', name: 'ProfileMenuPopoverPage', segment: 'profile-menu-popover', priority: 'low', defaultHistory: [] },
    { loadChildren: '../pages/side-menu/side-menu.module.ngfactory#SideMenuPageModuleNgFactory', name: 'SideMenuPage', segment: 'side-menu', priority: 'low', defaultHistory: [] },
    { loadChildren: '../pages/profile/profile.module.ngfactory#ProfilePageModuleNgFactory', name: 'ProfilePage', segment: 'profile', priority: 'low', defaultHistory: [] },
    { loadChildren: '../pages/welcome/welcome.module.ngfactory#WelcomePageModuleNgFactory', name: 'WelcomePage', segment: 'welcome', priority: 'low', defaultHistory: [] }
  ]
}),
        HttpModule,
        HttpClientModule
    ],
    bootstrap: [ IonicApp ],
    entryComponents: [
        AgoraMobile
    ],
    providers: [
        StatusBar,
        SplashScreen,
        InAppBrowser,
        { provide: ErrorHandler, useClass: IonicErrorHandler },
        GemProvider,
        CardProvider,
        HeroProvider,
        AgoraCacheProvider,
        EpicAccountProvider,
        SideMenuProvider,
        ProfileProvider,
        AppProvider,
        StatsProvider,
        MatchProvider,
        LeaderboardProvider
    ]

})
export class AppModule {
}


